* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.layout {
  margin-top: 100px;
  display: flex;
  flex: 1;
  border-style: solid;
  border-color: red;
}

.container {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: overlay;
  flex-direction: column;
  border-style: solid;
  border-color: aqua;
}

.contents {
  flex: 1;
  border-style: solid;
  border-color: burlywood;
}