.header {
  /* display: flex; */
  position: fixed;
  top:0;
  width: 100%;
  height: 100px;
  border-style: solid;
  border-color: blue;
  padding: 0 16px;
  /* align-items: center;
  justify-content: space-between; */
}

.logo {
  display: inline-block;
  margin: 8px 0 0 0;
  padding: 0 30px;
  color: rgba(0,0,0,0.4);
  text-decoration: none;
}

.logo > img { 
  width: 85px;
}

.logo > .brand-name {
  float: right;
  padding: 24px 0 0 7px;
}

nav {
  float: right;
}

.nav-items {
  margin: 16px 0 0 0;
}

.nav-items > li {
  display: inline-block;
}

.nav-items > li > a {
  text-decoration: none;
  line-height: 60px;
  padding: 0 30px;
  color: rgba(0,0,0,0.4);
}

.nav-items > li > a:hover {
  color: rgba(0,0,0,0.8);
}
